.social-media {
  color: #fff;
  display: none;
  font-size: 75%;
  font-weight: 500;
  line-height: 1;
  position: fixed;
  right: 0;
  text-align: center;
  text-transform: uppercase;
  top: 50%;
  transform: translateY(-50%);

  a {
    color: inherit;
    display: block;
    padding: 10px 15px;
    transition: padding .2s ease;

    &:hover,
    &:focus {
      padding-left: 20px;
      padding-right: 20px;
      text-decoration: none;
    }
  }

  img {
    display: block;
    margin: 0 auto 10px;
    max-width: 28px;
  }

  .facebook {
    background-color: #3664a2;
  }
}

@media (min-width: 992px) {
  .social-media {
    display: block;
  }
}

.follow-us {
  margin-bottom: 30px;
  text-align: center;

  .list-inline {
    font-size: 75%;
    font-weight: bold;
    text-transform: uppercase;
  }

  a {
    color: inherit;

    &:hover,
    &:focus {
      text-decoration: none;
    }
  }

  img {
    display: block;
    margin: 0 auto 5px;
    max-width: 28px;
  }
}
